//
// Navbar
// --------------------------------------------------

body {
  font-family: "Avenir", "Helvetica", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'GothamBook', "Helvetica", sans-serif;
  font-weight: bold;
}
.nav {
  z-index: @zIndex-10;
}

.navbar-default {
  color: white;
  background-color:@purple;
  width:100%;
}

.navbar-default a,
.navbar-default a:visited,
.navbar-default .navbar-nav > li > a,
.navbar-default .navbar-nav > li > a:visited {
  color:white!important;
}

.navbar-default .navbar-toggle {
  border-color:white;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: white;
}

.navbar-default .navbar-toggle:focus,
.navbar-default .navbar-toggle:hover {
  background-color:@light-blue;
}

.active,
.navbar-default .navbar-nav > .active > a{
  background-color: @purple;
  font-size: 1.6em;
}

.sticky {
  top:0;
}

.full-section {
  min-height:100vh;
  width:100%;
}

.part-section {
  height:calc(100vh - 100px);
  width:100%;
}


.logo-img {
  max-width:100%;
  max-height:100%;
  position: relative;
  top: 50%;
  .m-translateY(-50%);
}

.section {
  padding:50px 0;
}

.section-blue {
  background-color: @blue;
}

.section-green {
  background-color: @green;
}

.section-light-blue {
  background-color: @light-blue;
}

.section-orange {
  background-color: @orange;
}

.section-purple {
  background-color: @purple;
}

.section-red {
  background-color: @red;
}

.section-yellow {
  background-color: @yellow;
}


.large-text {
  font-weight:100;
  font-size:3vh;
  text-align: justify;
}

.section-img {
  margin:50px;
}
.section-text {
  vertical-align: middle;
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.cmn-graphic {
  margin:20px auto;
  width:100%;
}

@media (max-width: 768px) {
  .navbar-default {
    position: fixed;
    top:0;
  }
  .part-section {
    height:100vh;
  }
}


@media(min-width:769px) {
  .logo-vertical {
    display:none;
  }
}

@media (min-width:996px) {
  .nav {
      margin: 0;
      display: table;
      width: 100%;
      height:50px!important;
      overflow:hidden;
  }

  .nav li {
      display: table-cell;
      float: none;
      overflow:hidden;
      vertical-align: top;
  }
}


// handheld media queries
// portrait
@media handheld and (orientation: portrait) {
  .logo-vertical {
    display:inline-block!important;
  }
  .logo-landscape {
    width:80%;
    margin: 2% 0 0 0;
    display:none!important;
  }
}

// landscape
@media handheld and (orientation: landscape) {
  .logo-vertical {
    display:none!important;
  }
  .logo-landscape {
    display:inline-block!important;
  }
}



// iPhone media queries
// portrait
@media only screen
and (min-device-width : 320px)
and (max-device-width : 966px)
and (orientation : portrait) {
  .logo-vertical {
    display:inline-block!important;
  }
  .logo-landscape {
    display:none!important;
  }
}

// landscape
@media only screen
and (min-device-width : 320px)
and (max-device-width : 966px)
and (orientation : landscape) {
  .logo-vertical {
    display:none!important;
  }
  .logo-landscape {
    width:80%;
    margin: 2% 0 0 0;
    display:inline-block!important;
  }
}
